#page-loader:after {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  margin-top: -30px;
  margin-left: -30px;
  width: 60px;
  height: 60px;
  background-color: #1b8bf9;
  border-radius: 100%;
  content: '';
